<template>
   <div class="common-table">
       <el-table
       :data="tableData"
       height="90%"
       stripe
       >
        <el-table-column
        show-overflow-tooltip
        v-for="item in tableLabel"
        :key="item.prop"
        :label="item.label"
        :width="item.width ? item.width:125"
        >
            <template slot-scope="scope">
                <span style="margin-left:10px">{{scope.row[item.prop]}}</span>
            </template>
        </el-table-column>
        <el-table-column 
        label="manage"
        min-width="180">
        <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)">edit</el-button>
            <el-button size="mini" @click="handleDelete(scope.row)">delete</el-button>
        </template>
        </el-table-column>
       </el-table>
       <el-pagination
        class="pager"
        layout="prev,pager,next"
        :total="config.total"
        :current-page.sync="config.page"
        @current-change="changePage"
        :page-size="20"
       >

       </el-pagination>
   </div>
</template>
<style lang="less" scoped>
    .common-table{
        height:70vh;
        background-color: #fff;
        position: relative;
        .pager{
            position:absolute;
            bottom:0;
            right:20px;
        }
    }
</style>
<script>
    export default {
        name:"commonTable",
        props:{
            tableData:Array,
            tableLabel:Array,
            config:Object
        },
        data(){
            return {}
        },
        methods:{
            handleEdit(row){
                this.$emit('edit', row)
            },
            handleDelete(row){
                this.$emit('delete', row)
            },
            changePage(){

            }
        }
    }
</script>